
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "FoundPlayers",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["AllFoundPlayers"]),
  },
  methods: {
    ...mapMutations(["ALL_FOUND_PLAYERS"]),
    toggleAllFoundPlayers() {
      this.ALL_FOUND_PLAYERS();
    },
  },
});
