
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "AllFoundTeams",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["AllFoundTeams"]),
  },
  methods: {
    ...mapMutations(["ALL_FOUND_TEAMS"]),
    toggleAllFoundTeams() {
      this.ALL_FOUND_TEAMS();
    },
  },
});
